@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap');

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(212, 255, 241);
  font-family: "DM Sans", sans-serif;
}

.heading {
  font-size: 40px;
  margin: 30px 0;
  color: rgb(0, 0, 0);
  z-index: 1;
  text-align: center;
  font-weight: bold;
}

@media (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }
}